html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}
/* Extract from normalize.css by Nicolas Gallagher and Jonathan Neal git.io/normalize */
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%}body{margin:0}
article,aside,details,figcaption,figure,footer,header,main,menu,nav,section{display:block}summary{display:list-item}
audio,canvas,progress,video{display:inline-block}progress{vertical-align:baseline}
audio:not([controls]){display:none;height:0}[hidden],template{display:none}
a{background-color:transparent}a:active,a:hover{outline-width:0}
abbr[title]{border-bottom:none;text-decoration:underline;text-decoration:underline dotted}
b,strong{font-weight:bolder}dfn{font-style:italic}mark{background:#ff0;color:#000}
small{font-size:80%}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline}
sub{bottom:-0.25em}sup{top:-0.5em}figure{margin:1em 40px}img{border-style:none}
code,kbd,pre,samp{font-family:monospace,monospace;font-size:1em}hr{box-sizing:content-box;height:0;overflow:visible}
button,input,select,textarea,optgroup{font:inherit;margin:0}optgroup{font-weight:bold}
button,input{overflow:visible}button,select{text-transform:none}
button,[type=button],[type=reset],[type=submit]{-webkit-appearance:button}
button::-moz-focus-inner,[type=button]::-moz-focus-inner,[type=reset]::-moz-focus-inner,[type=submit]::-moz-focus-inner{border-style:none;padding:0}
button:-moz-focusring,[type=button]:-moz-focusring,[type=reset]:-moz-focusring,[type=submit]:-moz-focusring{outline:1px dotted ButtonText}
fieldset{border:1px solid #c0c0c0;margin:0 2px;padding:.35em .625em .75em}
legend{color:inherit;display:table;max-width:100%;padding:0;white-space:normal}textarea{overflow:auto}
[type=checkbox],[type=radio]{padding:0}
[type=number]::-webkit-inner-spin-button,[type=number]::-webkit-outer-spin-button{height:auto}
[type=search]{-webkit-appearance:textfield;outline-offset:-2px}
[type=search]::-webkit-search-decoration{-webkit-appearance:none}
::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}





/* APP */
.App {
  text-align: center;
  background-color: #000505;
}

/* HEADER */
.App-header {
  background-color: #282c34;
  background-image: url("img/lab05.png");
  background-size: cover;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 2px;
}

.corner-props-div{
  padding:72px 24px!important;
}

.corner-props{
  font-size: 13px;
}

.peta-logo {
  width: 24px;
  height: 32px;
  margin-right: 4px; 
}

/* FONTS */
@font-face {
  font-family: pixelBig;
  src: url("./fonts/PressStart2P-Regular.ttf");
}
@font-face {
  font-family: pixelSmall;
  src: url("./fonts/VT323-Regular.ttf");
}


/* BODY */
html,body{height: 100%;font-family: "pixelBig", sans-serif;font-size:13px;line-height:1.5}html{overflow-x:hidden}
h1{font-size:42px}h2{font-size:30px}h3{font-size:24px}h4{font-size:20px}h5{font-size:16px}h6{font-size:16px}
.tiny-font{font-size:9px}
.serif{font-family:serif}.sans-serif{font-family:sans-serif}.cursive{font-family:cursive}.monospace{font-family:monospace}
h1,h2,h3,h4,h5,h6{font-family:"pixelBig";font-weight:400;margin:10px 0}.wide{letter-spacing:4px}
hr{border:0;border-top:1px solid #eee;margin:20px 0}
img{vertical-align:middle}a{color:inherit}


.under-badge,.under-tag{background-color:#000808;color:#fff;display:inline-block;padding-left:12px;padding-right:8px;text-align:center}.under-badge{border-radius:2%}

.black,.hover-black:hover{color:#fff!important;background-color:#000808!important}
.container:after,.container:before,.panel:after,.panel:before,.row:after,.row:before,.row-padding:after,.row-padding:before,
.container,.panel{padding:0.01em 16px}.panel{margin-top:16px;margin-bottom:16px}

.padding-small{padding:4px 8px!important}.padding{padding:8px 16px!important}.padding-large{padding:12px 24px!important}
.padding-16{padding-top:16px!important;padding-bottom:16px!important}.padding-24{padding-top:24px!important;padding-bottom:24px!important}
.padding-32{padding-top:32px!important;padding-bottom:32px!important}.padding-48{padding-top:48px!important;padding-bottom:48px!important}
.padding-64{padding-top:64px!important;padding-bottom:64px!important}.padding-80{padding-top:80px!important;padding-bottom:80px!important}
.padding-top-64{padding-top:64px!important}.padding-top-48{padding-top:48px!important}
.padding-top-32{padding-top:32px!important}.padding-top-24{padding-top:24px!important}

.display-bottomleft{position:absolute;left:0;bottom:0}.display-bottomright{position:absolute;right:0;bottom:0}

.left-align{text-align:left!important}.right-align{text-align:right!important}.justify{text-align:justify!important}.center{text-align:center!important}
.text-white,.hover-text-white:hover{color:#fff!important}

.content,.auto{margin-left:auto;margin-right:auto}.content{max-width:890px}.auto{max-width:1140px}

.bar{width:100%;overflow:hidden}.center .bar{display:inline-block;width:auto}
.bar-block.center .bar-item{text-align:center}.block{display:block;width:100%}
.round-small{border-radius:2px}.round,.round-medium{border-radius:4px}.round-large{border-radius:8px}.round-xlarge{border-radius:16px}.round-xxlarge{border-radius:32px}

.row-padding,.row-padding>.half,.row-padding>.third,.row-padding>.twothird,.row-padding>.threequarter,.row-padding>.quarter,.row-padding>.col{padding:0 8px}

.col,.half,.third,.twothird,.threequarter,.quarter{float:left;width:100%}

.col.m1{width:8.33333%}.col.m2{width:16.66666%}.col.m3,.quarter{width:33.33333%}.col.m4,.third{width:33.33333%}.col.m5{width:41.66666%}.col.m6,.half{width:49.99999%}.col.m7{width:58.33333%}.col.m8,.twothird{width:66.66666%}.col.m9,.threequarter{width:74.99999%}.col.m10{width:83.33333%}.col.m11{width:91.66666%}.col.m12{width:99.99999%}

@media (min-width:601px){.quarter{width:24.99999%}}

.margin{margin:16px!important}.margin-top{margin-top:16px!important}.margin-bottom{margin-bottom:16px!important}
.margin-left{margin-left:16px!important}.margin-right{margin-right:16px!important}

.text-green,.hover-text-green:hover{color:#4CAF50!important}

.top,.bottom{position:fixed;width:100%;z-index:1}.top{top:0}.bottom{bottom:0}
